import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import {
  productListDetail,
  updateProductForReviews,
} from "../actions/productAction";
import { addToCart } from "../actions/cartAction";
import { AddToWishlist } from "../actions/wishlistAction";
import { FaBackward } from "react-icons/fa";
import { BiCart, BiCommentDots, BiHeart, BiUser } from "react-icons/bi";
import { MdOutlineReviews } from "react-icons/md";
import Rating from "../components/Rating";

const ProductDetail = ({ match, history }) => {
  const [qty, setQty] = useState(1);
  const [rating, setRating] = useState(0);
  const [comment, setComment] = useState("");

  const dispatch = useDispatch();
  const { product, loading, error } = useSelector(
    (state) => state.productDetail
  );
  const {
    loading: reviewLoading,
    error: errorReviews,
    success: successReview,
  } = useSelector((state) => state.productReviews);
  const user = useSelector((state) => state.user);

  const { images } = product;

  useEffect(() => {
    if (successReview) {
      alert("Review Submitd !");
      setRating(0);
      setComment("");
      dispatch({ type: "product_review_reset" });
    }
    dispatch(productListDetail(match.params.id));
  }, [dispatch, match, successReview]);

  const addToCartHandler = (e) => {
    e.preventDefault();
    if (user && user._id) {
      if (qty > product.countInStock) {
        toast.error(`We have just ${product.countInStock} products`);
      } else {
        history.push(`/cart`);
        dispatch(addToCart(match.params.id, qty, user._id));
      }
    } else {
      toast.info("For add to cart, You must be login");
    }

    // create cart array
  };

  const wishlistHandler = (e) => {
    e.preventDefault();
    if (!user) {
      toast.info(`Login First`);
    } else {
      AddToWishlist(match.params.id, user.token).then((res) => {
        toast.success(`Added to wishhlist`);
        // history.push('/wishlist')
      });
    }
  };

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(updateProductForReviews(match.params.id, { rating, comment }));
  };


  return (
    <>
      {loading ? (
        <p>loading...</p>
      ) : error ? (
        toast.error(`${error}`)
      ) : (
        <section>
          <div className="relative max-w-screen-xl px-4 py-8 mx-auto">
            <div className="py-5">
              <Link to="/">
                <FaBackward color="blue" />
              </Link>
            </div>

            <div className="grid items-start grid-cols-1 gap-8 md:grid-cols-2">
              <div className="grid grid-cols-2 gap-4 md:grid-cols-1">
                <img
                  alt="Les Paul"
                  src={
                    product &&
                    product.images &&
                    product.images &&
                    product.images[0].url
                  }
                  className="object-cover w-full aspect-square rounded-xl"
                />

                {product &&
                  product.images &&
                  product.images.length > 1 &&
                  product.images.map((i) => (
                    <div
                      className="grid grid-cols-2 gap-4 lg:mt-4"
                      key={i.public_id}
                    >
                      <img
                        alt="Les Paul"
                        src={i.url}
                        className="object-cover w-full aspect-square rounded-xl"
                      />
                    </div>
                  ))}
              </div>

              <div className="sticky top-0">
                <div className="flex justify-between mt-8">
                  <div className="max-w-[35ch]">
                    <h1 className="text-2xl font-bold">{product.name}</h1>

                    <Link
                      to={`/category/${product.category}`}
                      className="mt-0.5 text-sm"
                    >
                      {" "}
                      {product.category}
                    </Link>
                    <br />
                    {product.category && product.categoryName && (
                      <Link
                        to={`/category/${product.category}`}
                        className="mt-0.5 text-sm"
                      >
                        {" "}
                        {product.category}
                      </Link>
                    )}
                    <div className="border-b border-t my-2 py-1 border-gray-200">
                      <p
                        to={`/category/${product.category}`}
                        className="mt-0.5 text-xl  "
                      >
                        Brand : <strong> {product.brand} </strong>
                      </p>

                      <p
                        to={`/category/${product.category}`}
                        className="mt-0.5 text-xl  "
                      >
                        Stock :{" "}
                        <strong>
                          {product.countInStock > 0
                            ? "In Stock"
                            : "Out Of Stock"}{" "}
                        </strong>
                      </p>
                    </div>

                    <div className="border-b border-t my-2 py-1 border-gray-200">
                      <Rating
                        rating={product.rating}
                        reviews={product.numReviews}
                      />
                    </div>

                    {/* countInStock */}
                  </div>

                  <p className="text-lg font-bold"> {product.price} PKR</p>
                </div>

                <details className="group relative mt-4 [&_summary::-webkit-details-marker]:hidden">
                  <summary className="block">
                    <div>
                      <div className="prose max-w-none group-open:hidden">
                        <p>{product.description}</p>
                      </div>
                    </div>
                  </summary>
                </details>

                <form className="mt-8">
                  <div className="flex mt-8">
                    <div>
                      <label for="quantity" className="sr-only">
                        {product.countInStock}
                      </label>

                      <input
                        type="number"
                        as="select"
                        value={qty}
                        onChange={(e) => setQty(e.target.value)}
                        min="1"
                        className="w-12 rounded border-gray-200 py-3 text-center text-xs [-moz-appearance:_textfield] [&::-webkit-outer-spin-button]:m-0 [&::-webkit-outer-spin-button]:appearance-none [&::-webkit-inner-spin-button]:m-0 [&::-webkit-inner-spin-button]:appearance-none"
                      />
                    </div>

                    {user && !user.isAdmin && (
                      <>
                        <button
                          type="submit"
                          onClick={(e) => addToCartHandler(e)}
                          style={{
                            display: "flex",
                            flexDirection: "row",
                            justifyContent: "center",
                            alignItems: "center",
                            gap: "10px",
                          }}
                          disabled={product.countInStock === 0}
                          className="block px-5 py-3 ml-3 text-xs font-medium text-white bg-indigo-600 rounded hover:bg-indigo-500"
                        >
                          Add to Cart <BiCart />
                        </button>

                        <button
                          type="submit"
                          onClick={(e) => wishlistHandler(e)}
                          style={{
                            display: "flex",
                            flexDirection: "row",
                            justifyContent: "center",
                            alignItems: "center",
                            gap: "10px",
                          }}
                          className="block px-5 py-3 ml-3 text-xs font-medium text-white bg-pink-600 rounded hover:bg-pink-500"
                        >
                          Wishlist <BiHeart />
                        </button>
                      </>
                    )}
                    <br />
                    <hr />
                    <br />
                  </div>
                </form>

                <div className="bg-gray-100 px-5 mt-10 py-10">
                  <h2
                    className="text-xl"
                    style={{
                      display: "flex",
                      alignItems: "center",
                      gap: "5px",
                    }}
                  >
                    Products Reviews <MdOutlineReviews color="blue" />{" "}
                  </h2>
                  <hr />

                  {/* review form */}

                  {errorReviews && (
                    <p className="text-red-700">{errorReviews}</p>
                  )}

                  {user ? (
                    <>
                      {" "}
                      <form onSubmit={submitHandler}>
                        {reviewLoading && (
                          <p className="text-green-700"> Loading... </p>
                        )}
                        <div className="relative mb-4">
                          <label
                            for="full-name"
                            className="leading-7 text-sm text-gray-600"
                          >
                            Rate the product
                          </label>
                          <select
                            name="rating"
                            id="rating"
                            as="select"
                            value={rating}
                            onChange={(e) => setRating(e.target.value)}
                            required
                            className="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                          >
                            <option value=""> select...</option>
                            <option value="1"> 1 - Poor </option>
                            <option value="2"> 2 - Fair </option>
                            <option value="3"> 3 - Good </option>
                            <option value="4"> 4 - Very Good </option>
                            <option value="5"> 5 - Excellent </option>
                          </select>
                        </div>

                        <div className="relative mb-4">
                          <label
                            for="full-name"
                            className="leading-7 text-sm text-gray-600"
                          >
                            What you say about the product ?
                          </label>
                          <textarea
                            as="textarea"
                            row="3"
                            value={comment}
                            onChange={(e) => setComment(e.target.value)}
                            className="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                          />
                        </div>
                        <button
                          type="submit"
                          className="text-white bg-indigo-500 border-0 py-2 px-8 focus:outline-none hover:bg-indigo-600 rounded text-lg"
                        >
                          Save
                        </button>
                      </form>
                    </>
                  ) : (
                    <p className="text-indigo-700">
                      {" "}
                      Please login first!{" "}
                      <Link to="/login" className="mx-2">
                        {" "}
                        Login{" "}
                      </Link>{" "}
                    </p>
                  )}
                </div>

                <div className="bg-gray-100 px-5 mt-10 py-10">
                  <h2
                    className="text-xl"
                    style={{
                      display: "flex",
                      alignItems: "center",
                      gap: "5px",
                    }}
                  >
                    User Comments <BiCommentDots color="blue" />{" "}
                  </h2>
                  <hr />
                  <div class="p-2  w-full">
                    {product &&
                      product.reviews.map((x) => (
                        <div
                          key={x._id}
                          className="h-full flex items-center border-gray-200 border p-4 rounded-lg"
                        >
                          <BiUser className="mx-2" size={50} color="blue" />
                          <div className="flex-grow">
                            <h2 className="text-gray-900 title-font font-medium">
                              {x.name} {x.createdAt.substring(0, 10)}
                            </h2>
                            <p class="text-gray-500 text-sm"> {x.createdAt.substring(0, 10)}</p>
                            <p class="text-gray-700 text-lg"> {x.comment}</p>
                            <Rating rating={x.rating} />
                          </div>
                        </div>
                      ))}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      )}
    </>
  );
};

export default ProductDetail;
